import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { OpenProgramSetupComponent } from './open-program-setup.component';
import { AddOpenProgramComponent } from './add-open-program/add-open-program.component';
import { FilterOpenProgramComponent } from './filter-open-program/filter-open-program.component';

import { OpenProgramSetupResolver } from './open-program-setup.resolver';
import { OpenProgramSetupFilterResolver } from './open-program-setup.resolver';


const routes: Routes = [
  { path: '', component: OpenProgramSetupComponent, canActivate: [AuthGuard],
  children: [
    {
      path         : ':type/:id',
      component    : AddOpenProgramComponent,
      resolve: {
        OpenProgramSetupResolver
      }
    },
    {
      path         : 'filter',
      component    : FilterOpenProgramComponent,
      resolve: {
        OpenProgramSetupFilterResolver
      }
    }
  ]}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OpenProgramSetupRoutingModule { }
