import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { OpenProgramSetupService } from './open-program-setup.service';
import { SharedService } from 'src/app/shared/services/shared.service';


@Injectable({
  providedIn: 'root'
})

export class OpenProgramSetupResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private openProgramSetupService: OpenProgramSetupService,
    private sharedService:SharedService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.openProgramSetupService.getOpenProgramSetupMatDrawer();

    if(data == null){

      const parentUrl = state.url.split('/').slice(0, -3).join('/');

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;

  }

}

