import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

import { MatDrawer } from '@angular/material/sidenav';

import { ProgramService } from 'src/app/api/controllers/Program';
import { OpenProgramSetupService } from './open-program-setup.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

import * as moment from 'moment';

@Component({
  selector: 'open-program-setup',
  templateUrl: './open-program-setup.component.html',
  styleUrls: ['./open-program-setup.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class OpenProgramSetupComponent implements OnInit {

  isDataLoaded:boolean = true;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  _programsList:any;
  fullProgramsList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  groupData: string = "";
  allData: any;

  programList = [];

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private programService:ProgramService,
    private openProgramSetupService: OpenProgramSetupService,
    private sessionService: SessionService,
    private sharedService:SharedService,
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._programsList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  get programs$(): Observable<any>{
    return this._programsList.asObservable();
  }

  getIndexParams(event){

    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;

  }

  getProgram(id) {

    var data = this.programList.filter(item => {
        return item.id === parseInt(id) ? item : '';
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['/open-programs/setup'], {relativeTo: route});
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  addOpenProgram() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    var id = this.sharedService.guid();

    this.openProgramSetupService.setOpenProgramSetupMatDrawer({id: id, data: null})
    this._router.navigate(['new' , id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  viewOpenProgram(program) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    var id = this.sharedService.guid();

    this.openProgramSetupService.setOpenProgramSetupMatDrawer({id: id, data: program})
    this._router.navigate(['view' , id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  editOpenProgram(program) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    var id = this.sharedService.guid();

    this.openProgramSetupService.setOpenProgramSetupMatDrawer({id: id, data: program})
    this._router.navigate(['edit' , id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  filterOpenProgram(program) {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    var id = this.sharedService.guid();

    this.openProgramSetupService.setOpenProgramSetupFilterMatDrawer({id: id, data: null})
    this._router.navigate(['filter'], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  deleteOpenProgram(id) {
    this.modalsService.showDeleteConfirmModal(id)
  }

  exportData(event) {

  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  getExpiry(start, end) {
    let x = moment(start)
    let y = moment(end)
    return y.diff(x, 'days')
  }

  onGlSearchFilter(event) {

  }

  ngOnInit(): void {

    let res = [
      {
        "id": 1,
        "programId": 1,
        "name": "Super Package",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "endDate": "2021-04-06T08:57:32.270Z",
        "cost": 300,
        "discount": 100,
        "numberOfClass": 5,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1

      },
      {
        "id": 2,
        "programId": 2,
        "name": "Premium Package",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "endDate": "2021-04-21T08:57:32.270Z",
        "cost": 300,
        "discount": 100,
        "numberOfClass": 5,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
    ];

    this.fullProgramsList = res;
    this.allData = res;
    this._programsList.next(res);

    this.programs$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    })

    let prorgramListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.programService.getProgramList(prorgramListParams).subscribe((res:any) => {

      if(res.value) {
        this.programList = res.value;
      }

    })

    this.deleteSubscribe = this.modalsService.deleteindexcast.subscribe((id:any) => {


      if(id != null) {

        this.fullProgramsList = this.fullProgramsList.filter(item => {
          return item.id != id
        })
        this._programsList.next(this.fullProgramsList);

      }

    })


  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }


}
