import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { OpenProgramSetupComponent } from 'src/app/modules/admin/open-program/open-program-setup/open-program-setup.component';

import { ProgramService } from 'src/app/api/controllers/Program';
import { OpenProgramSetupService } from 'src/app/modules/admin/open-program/open-program-setup/open-program-setup.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';


@Component({
  selector: 'app-add-open-program',
  templateUrl: './add-open-program.component.html',
  styleUrls: ['./add-open-program.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AddOpenProgramComponent implements OnInit {

  isProgramSubmitted:boolean = true;
  isEdit:boolean = false;
  isView:boolean = false;

  program:any = {};

  programList = [];

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private programService:ProgramService,
    private _changeDetectorRef: ChangeDetectorRef,
    private _openProgramSetupComponent: OpenProgramSetupComponent,
    private openProgramSetupService: OpenProgramSetupService,
    private sessionService: SessionService,
    private sharedService:SharedService
  ) { }

  goBack() {
    this._openProgramSetupComponent.matDrawer.close();
    this.openProgramSetupService.setOpenProgramSetupMatDrawer(null)
    this._router.navigate(['/open-programs/setup']);
  }

  getProgram(id) {

    var data = this.programList.filter(item => {
        return item.id === parseInt(id) ? item : '';
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  getExpiry(start, end) {
    let x = moment(start)
    let y = moment(end)
    return y.diff(x, 'days')
  }

  getPackageStartDate(event) {

  }

  getPackageEndDate(event) {

  }

  onProgramChange(event) {
    this.program.programId = event[0].id
  }

  submitAddOpenProgramSetupForm(form) {

  }

  ngOnInit(): void {

    //Open the drawer
    this.openProgramSetupService.openprogramsetupdrawercast.subscribe(res => {

      if(res != null) {
        if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){
          this._openProgramSetupComponent.matDrawer.open();
          this.programList = this._openProgramSetupComponent.programList
          if(this._activatedRoute.params['value'].type == 'edit'){
            this.isEdit = true
            this.isView = false
            this.program = res.data
          }
          else if(this._activatedRoute.params['value'].type == 'view'){
            this.isView = true
            this.isEdit = false
            this.program = res.data
          }
          else {
            this.isEdit = false
            this.isView = false
            this.program = {}
          }
        }
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })


  }

}
