import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class OpenProgramSetupService {

  private openProgramSetupMatDrawer = new BehaviorSubject<any>(null);
  openprogramsetupdrawercast = this.openProgramSetupMatDrawer.asObservable();

  private openProgramSetupFilterMatDrawer = new BehaviorSubject<any>(null);
  openprogramsetupfiltermatdrawercast = this.openProgramSetupFilterMatDrawer.asObservable();

  constructor() { }

  getOpenProgramSetupMatDrawer(){
    return this.openProgramSetupMatDrawer.value
  }

  setOpenProgramSetupMatDrawer(value){
    this.openProgramSetupMatDrawer.next(value);
  }

  getOpenProgramSetupFilterMatDrawer(){
    return this.openProgramSetupFilterMatDrawer.value
  }

  setOpenProgramSetupFilterMatDrawer(value){
    this.openProgramSetupFilterMatDrawer.next(value);
  }

}
